import create from 'zustand';
import { devtools } from 'zustand/middleware';

export const useCartStore = create(
  devtools((set) => ({
    cart: [],
    products: [],
    fetchProducts: () => fetchProducts(set),
    addProduct: (product) => addProductToStore(set, product),
  })),
  'CartStore'
);

const addProductToStore = async (set, product) => {
  console.log('add', product);

  set((state) => ({ cart: [...state.cart, product] }), 'addProduct');
  // set((state) => ({ cart }), false, 'addProductToStore');
};

const fetchProducts = async (set) => {
  const response = await fetch('https://fakestoreapi.com/products');
  set({ products: await response.json() }, 'fetchProducts3');
};
