import { useEffect } from 'react';
import { useProductStore } from '../stores/product.store';
import { useCustomStore } from '../stores/custom.store';
import { useCartStore } from '../stores/cart.store';
import shallow from 'zustand/shallow';
import { Row, Col, List, Card, Image } from 'antd';
import { PlusCircleOutlined } from '@ant-design/icons';

const Products = () => {
  const { myVal, scalar } = useCustomStore(
    (state) => ({
      myVal: state.myVal,
      scalar: state.scalar,
    }),
    shallow
  );
  const { addProduct } = useCartStore(
    (state) => ({
      addProduct: state.addProduct,
    }),
    shallow
  );

  const { products, fetchProducts } = useProductStore(
    (state) => ({
      products: state.products,
      fetchProducts: state.fetchProducts,
      emptyProducts: state.emptyProducts,
    }),
    shallow
  );

  // const addProduct = useCartStore((state) => state.addProduct);
  useEffect(() => {
    fetchProducts();
  }, []);

  const onToggle = () => {
    scalar((state) => {
      state.myVal = 'nopes';
    });
  };
  const onAddProduct = (product) => (event) => {
    console.log({ product });
    addProduct(product);
  };
  return (
    <Row>
      <Col>
        <List
          grid={{
            gutter: 16,
            xs: 1,
            sm: 2,
            md: 3,
            column: 4,
          }}
          dataSource={products}
          renderItem={(product) => (
            <List.Item>
              <Card title={product.title} align='center' actions={[<PlusCircleOutlined key='add' onClick={onAddProduct(product)} />]}>
                <Image src={product.image} height={150} preview={false} width='auto' style={{ width: 'auto' }} />
              </Card>
            </List.Item>
          )}
        />
      </Col>
    </Row>
  );
};

export default Products;
